import React from 'react';
import classes from './WebsiteName.css';

const websiteName = (props) => (
    <div >
        <p className={classes.Aptly}><a href="/">APTLY</a></p>
        <p className={classes.Named}>NAMED</p>
    </div>
);

export default websiteName;


